import React, { useState, useEffect } from "react"
import axios from "axios"
const Dropdown = props => {
  const limit = 2
  const headers = {
    api_key: process.env.API_KEY,
    access_token: process.env.DELIVERY_TOKEN,
  }


  const dropdownRef = React.createRef()
  const [skips] = useState(0)
  const [category, setCategory] = useState("")

  useEffect(() => {
    if (dropdownRef.current.value === category) {
      if (props.dropdownList.includes(category)) {
        const url = `${process.env.BASEURL}&typeahead=${category}&skip=${skips}&limit=${limit}`
        console.log(url)

        props.callbackSelect(category)
        axios
          .get(url, { headers })
          .then(result => {
            props.callback(result.data.entries, result.data.count)
          })
          .catch(err => {
            console.error()
          })
      }
    }
  }, [category])

  function handleDropdown(evt) {
    setCategory(evt.target.value)
  }
  return (
    <aside>
      {console.log("Dropdown...")}
      <select name="Category" onChange={handleDropdown} ref={dropdownRef}>
        {props.dropdownList.map((el, i) => {
          return (
            <option value={el} key={i}>
              {el}
            </option>
          )
        })}
      </select>
    </aside>
  )
}
export default React.memo(Dropdown)
