import React, { useState } from "react"
import Link from "gatsby-link"
import { graphql } from "gatsby"
import LayoutAddon from "../components/layoutAddon"
import DropDown from "../components/dropdown"
import SEO from "../components/seo"
import Loadmore from "../components/loadmore"

const Blog = ({ data }) => {
  let previousDropdown
  const [category, setcategory] = useState(
    data.allContentstackGatsbyBlockpost.nodes[0].category
  )
  const [count, setcount] = useState()
  const [getResponse, setResponse] = useState(
    data.allContentstackGatsbyBlockpost.nodes
  )
  function dropdownCallBack(dataCategory, count) {
    setResponse([...dataCategory])
    setcount(count)
  }
  function categoryCallBack(params) {
    setcategory(params)
  }

  function loadmoreCallback(params) {
    setResponse([...params])
  }

  function renderElements(dataArray) {
    return dataArray.map((nodes, id) => {
      return (
        <div key={id}>
          <h2>{nodes.title}</h2>
          <p
            style={{
              width: "600px",
              overflow: "hidden",
              whiteSpace: "nowrap",
              textOverflow: "ellipsis",
            }}
          >
            {nodes.blog_content}
          </p>
          <Link to={nodes.url}>Read more...</Link>
          <br />
          <br />
        </div>
      )
    })
  }
  return (
    <LayoutAddon>
      <SEO title="Blog" />
      <div>
        {console.log("Blog....")}

        <h1>Trending Blogs!!!</h1>
        {previousDropdown !== getResponse ? (
          <DropDown
            dropdownList={
              data.allContentstackGatsbyDropdown.nodes[0].single_line
            }
            callback={dropdownCallBack}
            callbackSelect={categoryCallBack}
          />
        ) : null}

        {getResponse !== "" ? renderElements(getResponse) : null}
        {count <= getResponse.length ? null : (
          <Loadmore
            callbackFunction={loadmoreCallback}
            categorys={category}
            allCategory={
              data.allContentstackGatsbyDropdown.nodes[0].single_line
            }
            currentData={getResponse}
          />
        )}
      </div>
    </LayoutAddon>
  )
}

export const pageQuery = graphql`
  {
    allContentstackGatsbyBlockpost(
      limit: 2
      filter: { category: { eq: "Science Friction" } }
    ) {
      totalCount
      nodes {
        category
        blog_content
        title
        url
        uid
      }
    }
    allContentstackGatsbyDropdown {
      nodes {
        title
        single_line
      }
    }
  }
`
export default React.memo(Blog)
