import React, { useState, useEffect } from "react"
import axios from "axios"
const limit = 2
const headers = {
  api_key: process.env.API_KEY,
        access_token: process.env.DELIVERY_TOKEN,
}
const Loadmore = props => {
  // const buttonRef = React.createRef()

  const [skip, setSkip] = useState(0)
  const [category] = useState(props.categorys)

  useEffect(() => {
    if (skip !== 0) {
      const url =`${process.env.BASEURL}&typeahead=${category}&skip=${skip}&limit=${limit}`
    
      axios
        .get(url, { headers })
        .then(res => {
          const newdata = props.currentData
          newdata.push(...res.data.entries)
          props.callbackFunction(newdata)
        })
        .catch(() => {
          console.error()
        })
    }
  }, [skip])

  function handleLoadmore() {
    if (category !== props.categorys) {
      console.log("handle", category, props.categorys)

      setSkip(0)
    } else if (
      props.currentData.every(el => {
        return el.category === category
      })
    ) {
      setSkip(skip + limit)
    }
  }

  return (
    <div >
      {console.log("Loadmore....")}
      <button onClick={handleLoadmore}>View more</button>
    </div>
  )
}

export default React.memo(Loadmore)
